const Home = () => import('@/views/main.vue')
const Notfound = () => import('@/views/notfound.vue')
const routes = [];
routes.push({
    path: '*',
    component: Notfound,
    meta: {
        hidden: true
    }
});
const files = require.context("@/data", true, /\.json$/).keys();
for (let i = 0; i < files.length; i++) {
    const jsonfile = files[i]
    // console.log()
    const oneMenu = require("@/data/" + jsonfile.replace("./", ""))
    const childList = []
    if (oneMenu.children) {
        for (let j = 0; j < oneMenu.children.length; j++) {
            const child = oneMenu.children[j]
            childList.push(
                {
                    path: child.path,
                    component: Home,
                    meta: {
                        name: child.name,
                    }
                }
            )
    
        }

    }

    routes.push({
        path: oneMenu.root+oneMenu.path,
        component: Home,
        redirect: childList.length > 0 ? childList[0].path : '',
        meta: {
            name: oneMenu.name,
        },
        children:  childList.length > 0 ? childList : '',
    })
    // console.log(routes)
}
export default routes



