<template>
    <div>
        <el-row>
            <el-col style="padding-top: 1%;padding-left: 2%;" :span="2"> <img style="scale: 1.2;"
                    src="../../static/logo.svg" alt="BingX API Docs" class="logo"></el-col>
            <el-col :span="17"> <el-menu :default-active="'/' + $route.path.split('/')[1]" class="el-menu-demo"
                    mode="horizontal" router>
                    <el-menu-item index="/swap">USDT本位永续合约</el-menu-item>
                    <el-menu-item index="/swapV2">USDT本位永续合约V2</el-menu-item>
                    <el-menu-item index="/spot">现货</el-menu-item>
                    <el-menu-item index="/standard">标准合约</el-menu-item>
                    <el-menu-item index="/common">账户与钱包</el-menu-item>
                </el-menu></el-col>
            <el-col :span="3" class="el-menu-demo el-menu--horizontal el-menu">
                <div style="padding-top: 4%;text-align: right;margin-right: 3%;">
                    <el-select style="width: 100%;" @change="changeSearch()" clearable v-model="value" filterable remote reserve-keyword
                        placeholder="请输入关键词搜索" :remote-method="remoteMethod" :loading="loading">
                        <el-option v-for="item in options" :key="item.id + 'aa' + Math.random()" :label="item.label"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </div>
            </el-col>
            <el-col :span="2" style="border-bottom: solid 1px #e6e6e6;">
                <div style="padding-top: 5%;text-align: center;border-bottom: solid 1px #e6e6e6;margin-right: 13%;">

                    <el-select v-model="$store.getters.app.lang.value" placeholder="请选择" style="margin-right: 3%;">
                        <el-option v-for="item in $store.getters.app.lang.options" :key="item" :label="item" :value="item">
                        </el-option>
                    </el-select>
                </div>

            </el-col>

        </el-row>
    </div>
</template>
<script>
export default {
    watch: {
        '$store.getters.app.lang.value': function (to, from) {
            if (to == '中文简体') {
                this.lang = 'zh'
            } else if (to == '中文繁體') {
                this.lang = 'zh-hk'
            } else {
                this.lang = 'en'
            }
            this.init()
            // alert(from)
            //   if (to != from)
            //     this.$scrollTo('#' + this.$route.path.replace(/\//g, '_'), 10, { easing: 'ease', cancelable: false })
            //   if (to.split('/')[1] != from.split('/')[1]) {
            //     alert(to.split('/')[1])
            //     // this.$scrollTo('#'+this.$route.path.replace(/\//g,'_'), 1000, { easing: 'ease', cancelable: false })

            //   }
        }
    },
    created() { this.init() },
    data() {
        return {
            options: [],
            lang: "",
            value: '',
            input1: '',
            input2: '',
            input3: '',
            input4: '',
            options: [],
            api_list: [],
            value: [],
            list: [],
            loading: false,
            states: ["Alabama", "Alaska", "Arizona",
                "Arkansas", "California", "Colorado",
                "Connecticut", "Delaware", "Florida",
                "Georgia", "Hawaii", "Idaho", "Illinois",
                "Indiana", "Iowa", "Kansas", "Kentucky",
                "Louisiana", "Maine", "Maryland",
                "Massachusetts", "Michigan", "Minnesota",
                "Mississippi", "Missouri", "Montana",
                "Nebraska", "Nevada", "New Hampshire",
                "New Jersey", "New Mexico", "New York",
                "North Carolina", "North Dakota", "Ohio",
                "Oklahoma", "Oregon", "Pennsylvania",
                "Rhode Island", "South Carolina",
                "South Dakota", "Tennessee", "Texas",
                "Utah", "Vermont", "Virginia",
                "Washington", "West Virginia", "Wisconsin",
                "Wyoming"]
        }
    },
    mounted() {
        // this.list = this.states.map(item => {
        //     return { value: `value:${item}`, label: `label:${item}` };
        // });
    }, methods: {
        init() {
            if (this.$store.getters.app.lang.value == '中文简体') {
                this.lang = 'zh'
            } else if (this.$store.getters.app.lang.value == '中文繁體') {
                this.lang = 'zh-hk'
            } else {
                this.lang = 'en'
            }
            this.api_list = []
            const modulepath = this.$route.path.split('/')[1]
            const files = require.context("@/data", true, /\.json$/).keys();
            for (let i = 0; i < files.length; i++) {
                const jsonfile = files[i]
                if (jsonfile.indexOf("/" + modulepath + "/")) {
                    const oneMenu = require("@/data/" + jsonfile.replace("./", ""))
                    if (oneMenu.children && oneMenu.children.length > 0) {
                        for (let j = 0; j < oneMenu.children.length; j++) {
                            const data = {
                                root: oneMenu.root,
                                path: oneMenu.path,
                                name: oneMenu.name[this.lang],
                                api: '',
                                comment: '',
                                id: '',
                            }
                            const child = oneMenu.children[j]
                            data.path += "/" + child.path
                            if (child.comment)
                                data.comment = child.comment[this.lang]
                            if (child.api) {
                                data.api = child.api.uri
                                data.info = child.api.info[this.lang]
                            }
                            if (child.name)
                                data.name = child.name[this.lang]

                            data.id = (data.root + data.path).replace(/\//g, '_')
                            data.label = data.name + "|" + data.api + "|" + data.info + "|" + data.comment
                            this.api_list.push(data)
                        }

                    } else {
                        const data = {
                            root: oneMenu.root,
                            path: oneMenu.path,
                            name: oneMenu.name[this.lang],
                            api: '',
                            comment: '',
                        }
                        if (oneMenu.comment)
                            data.comment = oneMenu.comment[this.lang]
                        if (oneMenu.api) {
                            data.api = oneMenu.api.uri
                            data.info = oneMenu.api.info[this.lang]
                        }
                        data.id = (data.root + data.path).replace(/\//g, '_')
                        data.label = data.name + "|" + data.api + "|" + data.info + "|" + data.comment
                        this.api_list.push(data)
                    }


                }

            }

        },
        changeSearch() {
            this.$router.push( this.value.replaceAll("_","/"))
            // this.$scrollTo('#' + this.value, 10, { easing: 'ease', cancelable: false })
        },
        remoteMethod(query) {
            if (query !== '') {
                this.loading = true;
                setTimeout(() => {
                    this.loading = false;
                    this.options = this.api_list.filter(item => {
                        return item.label.toLowerCase()
                            .indexOf(query.toLowerCase()) > -1;
                    });
                }, 200);
            } else {
                this.options = [];
            }
        }
    }
};
</script>
<style>
.el-menu.el-menu--horizontal {
    border-bottom: none;
}

.el-menu {
    background-color: white;
}
</style>



