const app = {
  state: {
    sidebar: '1111',
    lang: {
      options: ["English", "中文繁體", "中文简体"],
      value: "中文简体"
    },
  },
  mutations: {
    SET_SIZE: (state, size) => {
      state.sidebar = 'change'
    }
  },
  actions: {
    setSize({ commit }, size) {
      commit('SET_SIZE', size)
    }
  }
}

export default app



