<template>
    <div style="padding-left: 10%;">
        <div>
            <div>USDT本位永续合约</div>
            <el-menu :default-active="$route.path" :default-openeds="level1" router>
                <template v-for="route in $router.options.routes" v-if="!route.meta.hidden">
                    <label v-if="!route.children || route.children.length == 1" :index="route.path">
                        <label v-if="$store.getters.app.lang.value == '中文简体'">
                            <el-menu-item :index="route.path" :key="route.path">
                                {{ route.meta.name.zh }}
                            </el-menu-item>
                        </label>
                        <label v-else-if="$store.getters.app.lang.value == '中文繁體'">
                            <el-menu-item :key="route.path">
                                {{ route.meta.name['zh-hk'] }}
                            </el-menu-item>
                        </label>
                        <label v-else>
                            <el-menu-item :index="route.path" :key="route.path">
                                {{ route.meta.name['en'] }}
                            </el-menu-item>
                        </label>
                    </label>

                    <el-submenu v-else :index="route.path">
                        <template v-if="$store.getters.app.lang.value == '中文简体'" slot="title">{{ route.meta.name.zh
                        }}</template>
                        <template v-else-if="$store.getters.app.lang.value == '中文繁體'" slot="title">{{
                            route.meta.name['zh-hk'] }}</template>
                        <template v-else slot="title">{{ route.meta.name.en }}</template>
                        <lable v-if="$store.getters.app.lang.value == '中文简体'">
                            <template slot="title">{{ route.meta.name.zh }}</template>
                            <el-menu-item v-for="child in route.children" v-if="!child.meta.hidden"
                                :index="route.path + '/' + child.path" :key="route.path + '/' + child.path">{{
                                    child.meta.name.zh
                                }}</el-menu-item>
                        </lable>
                        <lable v-else-if="$store.getters.app.lang.value == '中文繁體'">

                            <el-menu-item v-for="child in route.children" v-if="!child.meta.hidden"
                                :index="route.path + '/' + child.path" :key="route.path + '/' + child.path">{{
                                    child.meta.name['zh-hk']
                                }}</el-menu-item>
                        </lable>
                        <lable v-else>
                            <el-menu-item v-for="child in route.children" v-if="!child.meta.hidden"
                                :index="route.path + '/' + child.path" :key="route.path + '/' + child.path">{{
                                    child.meta.name.en
                                }}</el-menu-item>
                        </lable>
                    </el-submenu>
                </template>
            </el-menu>
        </div>
        <div>
            <div>Socket文档</div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {

            activities: [{
                content: '当前最新版本: 更新了....',
                timestamp: '2024-04-12',
                size: 'large',
                color: '#0bbd87'
            }, {
                content: '更改了..abc..',
                timestamp: '2023-04-03',
            }, {
                content: '更改了..123..',
                timestamp: '2022-04-03',
                size: 'large'
            }, {
                content: '更改了..lalala..',
                timestamp: '2021-04-03'
            }]
        };
    },
    computed: {
        level1() {
            return this.$router.options.routes.map(function (route) {
                return route.path;
            });
        }
    }
};
</script>
<style>
.el-submenu .el-menu {
    background-color: white;
}
</style>


